import {problems} from "./problems";

const generateProblem = () => {
    return problems[Math.floor(Math.random() * problems.length)];
};

const compare = (problem, firstAnswer, secondAnswer) => {
    const answers = problem.answers;
    let firstProblemAnswer = parseAnswer(answers[0]);
    let secondProblemAnswer = parseAnswer(answers[1]);

    let firstPlayerAnswer = parseAnswer(firstAnswer);
    let secondPlayerAnswer = parseAnswer(secondAnswer);

    return firstProblemAnswer === firstPlayerAnswer && secondProblemAnswer === secondPlayerAnswer;
}

const parseAnswer = (answer) => {
    return answer.toLowerCase().replaceAll(" ", "");
}

export default {
    generateProblem,
    compare
}