import React from "react";
import AnswerModal from "./AnswerModal";
import { MathHelper } from "../utils";

class Quiz extends React.Component {
  _isMounted = false;
  _secondsIntervalRef;

  state = {
    problem: "",
    answer: 0,
    firstAnswer: "",
    secondAnswer: "",
    modal: "",
    modalShowing: false,
    streaks: 0
  };

  earnLife = () => {
    this.props.onEarnLife();
    this.showModal("success", "3 Seguidas! Você ganhou um ♥");
    this.setState({
      streaks: 0
    });
  };

  correctAnswer = () => {
    if (this.state.streaks > 2) {
      this.earnLife();
    } else {
      this.showModal("success");
    }

    this._isMounted && this.props.onCorretAnswer();
    this.setState(state => {
      return {
        streaks: state.streaks + 1
      };
    });

    this.nextProblem();
  };

  componentDidMount() {
    this._isMounted = true;
    this.getProblem();
    this.firstAnswerInput.focus();
  }

  shouldComponentUpdate(nextProps) {
    if (this.props.lifes < 1) {
      this.props.onEndGame(this.state.points);
      return false;
    }
    return nextProps.lifes > -1;
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  wrongAnswer = () => {
    this._isMounted && this.props.onWrongAnswer();
    this.setState({
      streaks: 0
    });
    this.showModal("error", "Resposta errada! Tente novamente.");
    this.nextProblem();
  };

  nextProblem = () => {
    setTimeout(() => {
      this.getProblem();
      this._isMounted &&
        this.setState({
          modalShowing: false
        });
      if (this.props.lifes > 0) this.firstAnswerInput.focus();
    }, 1300);
  };

  evaluateProblem = () => {
    if (this.state.firstAnswer === "" || this.state.secondAnswer === "") {
      return;
    }
    if (MathHelper.compare(this.state.problem, this.state.firstAnswer, this.state.secondAnswer)) {
      this.resetAnswers();
      return this.correctAnswer();
    }
    this.resetAnswers();
    return this.wrongAnswer();
  };

  resetAnswers = () => {
    this.setState({
      firstAnswer: "",
      secondAnswer: ""
    });
  }

  handleFirstAnswer = ev => {
    const val = ev.target.value;
    this.setState({
      firstAnswer: val.replaceAll(" ", "")
    });
  };

  handleSecondAnswer = ev => {
    const val = ev.target.value;
    this.setState({
      secondAnswer: val.replaceAll(" ", "")
    });
  };

  showModal = (type, text) => {
    this.setState({
      modal: <AnswerModal type={type} text={text} />,
      modalShowing: true
    });
  };

  getProblem = () => {
    const newProblem = MathHelper.generateProblem();
    this._isMounted &&
      this.setState({
        problem: newProblem
      });
  };

  render() {
    return (
      <section className="show-up">
        <div>
          {this.state.modalShowing ? (
            this.state.modal
          ) : (
            <div>
              <h5 style={{ fontSize: "1em", fontWeight: "normal", maxWidth: "70vw"}}> {this.state.problem.description} </h5>

              <div id="inputs-container" style={{ display: "grid", gridTemplateColumns: "25% 50%"}}>
                <div id="symbol" style={{textAlign: "right", marginRight: "15px"}}>
                  <span className={"symbol"} style={{ }}>{ "{" }</span>
                </div>
                <div id="inputs" style={{ display: "grid", gridTemplateColumns: "auto"}}>
                  <input
                      ref={input => {
                        this.firstAnswerInput = input;
                      }}
                      className="App-input"
                      type="text"
                      placeholder="Ex.: x + y = 12"
                      onKeyUp={this.handleFirstAnswer}
                  />
                  <input
                      ref={input => {
                        this.secondAnswerInput = input;
                      }}
                      className="App-input"
                      type="text"
                      placeholder="Ex.: 2x + 4y = 24"
                      onKeyUp={this.handleSecondAnswer}
                  />
                </div>
              </div>
              <div className={"row"}>
                <button
                    className="App-link App-button"
                    onClick={this.evaluateProblem}
                    style={{ marginTop: "15px" }}
                    disabled={this.state.firstAnswer === "" || this.state.secondAnswer === ""}
                >
                  Responder
                </button>
              </div>
            </div>
          )}
        </div>
      </section>
    );
  }
}

export default Quiz;
